.wrraper{
    height: 100vh;
    background-color: blueviolet;
    margin: 0;
}
.title{
    margin-top: 0;
   text-align: center;
   padding-top: 50px;
   font-size: 40px;
   color: gold;
}
.form{
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: center;

}
.label{
    padding: 15px;
}
.span{
    display: block;
    font-size:large;
    color: gold;
}
.input{
    padding: 10px;
}
.button{
    display: inline-block;
    padding: 10px 30px;
    border: 2px solid gold;
    border-radius: 5px;
    background-color: gold;
    color: black;
    font-weight: 700;
}